<template>
  <div class="deliverytemplate">
    <div class="leftmenu">
      <h3>待处理sku</h3>
      <div>
        <el-form :inline="true" :model="formData" class="demo-form-inline">
          <el-form-item label="国家">
            <el-select
              @change="changeOpt('country')"
              v-model="formData.country"
              placeholder="请选择国家"
            >
              <el-option value="all" label="全部"></el-option>
              <el-option
                v-for="(item, index) in countries"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="站点">
            <el-select
              @change="changeOpt('store')"
              v-model="formData.store"
              placeholder="请选择站点"
            >
              <el-option value="all" label="全部"></el-option>
              <el-option
                v-for="(item, index) in stores"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <ul>
          <li
            :class="{ active: curIdx === i }"
            v-for="(item, i) in due_skus"
            :key="i"
          >
            <a @click="showSkuTem(i)">{{ item }}</a>
          </li>
        </ul>
        <!-- <div class="editlog">
          <el-button type="primary" class="search">操作记录</el-button>
        </div> -->
      </div>
    </div>
    <div class="rightcon">
      <el-form :inline="true" :model="formData2">
        <el-form-item label="更改原因">
          <el-input v-model="formData2.changereason" disabled></el-input>
        </el-form-item>
        <el-form-item label="模板类型">
          <el-input v-model="formData2.temtype" disabled></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="go_route('/SupervisorPage')"
            >操作记录</el-button
          >
        </el-form-item>
      </el-form>

      <div>
        <FbmTemplate
          @finalcheck="checkAllUpload"
          :allStepData="mdc"
        ></FbmTemplate>
      </div>
    </div>
  </div>
</template>

<script>
import FbmTemplate from "@/components/orderManagement/FBM/FbmTemplate";
export default {
  data() {
    return {
      formData: {
        store: "all",
        country: "all"
      },
      formData2: {
        changereason: "",
        temtype: ""
      },
      stores: [],
      countries: [],
      due_skus: [],

      skuData: [],
      sku: "",
      curIdx: 0,
      changereason: "",
      temtype: "",
      comName: "",
      showFinal: false,
      finalCommit: false,
      //   传入模板的对象
      allstepdata: {
        h2title: "（自动化，加急，隔日达，当天达 + 1天备货时间）",
        others: [
          {
            hTitle: "1、设置仓库",
            hTip:
              "点击设置-配送设置-一般配送设置-编辑-添加新地址，填写仓库地址。美西仓库地址：5201 Cliffwood Dr, Montclair,CA 91763, CLIFFWOOD, CA, 91763, 美国",
            imgSrc: ["mxc_step1.png"],
            uploadUrl: "xxx"
          },
          {
            hTitle: "2、配送模板",
            hTip: "创建新配送模板并命名",
            imgSrc: ["mxc_step2.png"],
            uploadUrl: "yyy"
          },
          {
            hTitle: "3、设置模板",
            hTip: "选择美西仓库-下一步-选择承运人配送服务（此处为FedEx配送）",
            imgSrc: ["mxc_step3_1.png", "mxc_step3_2.png"],
            uploadUrl: "zzz"
          },
          {
            hTitle: "4、下一步-完成",
            hTip:
              "勾选自动化，标准运输时间，设置为2-4天，偏远地区为5-8天，且运费为4.99，每磅0.99刀",
            imgSrc: ["mxc_step4_1.png"],
            uploadUrl: "mmm"
          },
          {
            hTitle: "5、库存-商品页面-选择相应的配送模板-报价-处理时间-1天",
            hTip: "",
            imgSrc: ["mxc_step4_1.png", "mxc_step4_2.png"],
            uploadUrl: "mmm"
          }
        ]
      },
      mxc_yc: {
        h2title:
          "（关闭自动化，关闭加急，关闭隔日达，关闭当天送达 + 5天备货时间）",
        others: [
          {
            hTitle: "1、创建新配送模板并命名",
            hTip: "",
            imgSrc: ["mxc_yc_step1.png"],
            uploadUrl: "xxx"
          },
          {
            hTitle: "2、设置模板",
            hTip: "选择美西仓库-下一步-选择承运人配送服务（此处为FedEx配送）",
            imgSrc: ["mxc_yc_step2_1.png", "mxc_yc_step2_2.png"],
            uploadUrl: "yyy"
          },
          {
            hTitle: "3、关闭自动化，关闭加急，关闭隔日达，关闭当天送达",
            hTip: "",
            imgSrc: ["mxc_yc_step3.png"],
            uploadUrl: "zzz"
          },
          {
            hTitle:
              "4、改时间运输时间，普通为5-8天，偏远地区7-14天，且运费为4.99，每磅0.99刀",
            hTip: "",
            imgSrc: ["mxc_yc_step4.png"],
            uploadUrl: "mmm"
          },
          {
            hTitle: "5、库存-商品页面-选择相应的配送模板-报价-处理时间-5天",
            hTip: "",
            imgSrc: ["mxc_yc_step5.png"],
            uploadUrl: "mmm"
          }
        ]
      },
      mdc: {
        h2title: "（自动化，加急，隔日达，当天达 + 1天备货时间）",
        others: [
          {
            hTitle: "1、设置仓库",
            hTip:
              "点击设置-配送设置-一般配送设置-编辑-添加新地址，填写仓库地址 美东仓库地址：12 McCullough Dr New Castle，DE 19720,New Castle, DE, 19720, 美国",
            imgSrc: ["mdc_step1.png"],
            uploadUrl: "xxx"
          },
          {
            hTitle: "2、配送模板",
            hTip: "创建新配送模板并命名",
            imgSrc: ["mdc_step2.png"],
            uploadUrl: "yyy"
          },
          {
            hTitle: "3、设置模板",
            hTip: "选择美东仓库-下一步-选择承运人配送服务（此处为FedEx配送）",
            imgSrc: ["mdc_step3_1.png", "mdc_step3_2.png"],
            uploadUrl: "zzz"
          },
          {
            hTitle: "4.下一步-完成",
            hTip:
              "勾选自动化，标准运输时间，设置为2-4天，偏远地区为5-8天，且运费为4.99，每磅0.99刀",
            imgSrc: ["mdc_step4_1.png", "mdc_step4_2.png"],
            uploadUrl: "mmm"
          },
          {
            hTitle: "5、库存-商品页面-选择相应的配送模板-报价-处理时间-1天",
            hTip: "",
            imgSrc: ["mdc_step5.png"],
            uploadUrl: "mmm"
          }
        ]
      },
      mdc_yc: {
        h2title:
          "（关闭自动化，关闭加急，关闭隔日达，关闭当天送达 + 5天备货时间）",
        others: [
          {
            hTitle: "1、创建新配送模板并命名",
            hTip: "",
            imgSrc: ["mdc_yc_step1.png"],
            uploadUrl: "xxx"
          },
          {
            hTitle: "2、设置模板",
            hTip: "选择美东仓库-下一步-选择承运人配送服务（此处为FedEx配送）",
            imgSrc: ["mdc_yc_step2_1.png", "mdc_yc_step2_2.png"],
            uploadUrl: "yyy"
          },
          {
            hTitle: "3、关闭自动化，关闭加急，关闭隔日达，关闭当天送达",
            hTip: "",
            imgSrc: ["mdc_yc_step3.png"],
            uploadUrl: "zzz"
          },
          {
            hTitle:
              "4、改时间运输时间，普通为5-8天，偏远地区7-14天，且运费为4.99，每磅0.99刀",
            hTip: "",
            imgSrc: ["mdc_yc_step4.png"],
            uploadUrl: "mmm"
          },
          {
            hTitle: "5、库存-商品页面-选择相应的配送模板-报价-处理时间-5天",
            hTip: "",
            imgSrc: ["mdc_yc_step5.png"],
            uploadUrl: "mmm"
          }
        ]
      }
    };
  },
  methods: {
    // 跳转到主管页面
    go_route(val) {
      this.$router.push(val);
    },
    // 显示订单模板
    showSkuTem(idx) {
      this.curIdx = idx;
      this.sku = this.due_skus[idx];
    },
    finallcheck() {
      if (!this.finalCommit) {
        alert("未上传全部图片,请检查！");
        return;
      }
      this.showFinal = true;
      // todo 是否需要发请求？列表中去除本待处理sku
      this.due_skus.splice(this.curIdx, 1);
    },
    gotIt() {
      this.showFinal = false;
    },
    checkAllUpload(count) {
      // 判断是否全部已上传，如果 不是，弹窗
      if (this.allstepdata.length === count.length) {
        this.finalCommit = true;
      }
    },
    changeOpt(val) {
      if (val === "country") {
        this.formData2.changereason = "";
        this.formData2.temtype = "";
        this.formData.store = "all";
        this.stores = [];
        this.due_skus = [];
        if (this.formData.country !== "all") {
          let url = this._url2 + "server/personnel_management/get_sign_area/";
          this.$axios
            .get(url, {
              params: {
                country: this.formData.country
              }
            })
            .then(res => {
              this.stores = res.data.data;
            });
        }
      } else if (val === "store") {
        this.due_skus = [];
        if (
          this.formData.country !== "all" &&
          this.formData.store &&
          this.formData.store !== "all"
        ) {
          let item = this.skuData.find(
            i =>
              i.station === this.formData.store &&
              i.country === this.formData.country
          );
          if (!item) return;
          item.skus.forEach(i => {
            this.due_skus.push(i.sku);
          });
          this.sku = this.due_skus[0];
          this.formData2.changereason = item.skus[0].change_reason;
          this.formData2.temtype = item.skus[0].template_type;
          // 设置模板 待处理
          this.mdc = this.mdc_yc;
        }
      }
    }
  },
  created() {
    // 获取国家站点
    let url = "server/order_management/get_template_data/";
    this.$axios.get(this._url2 + url).then(res => {
      this.skuData = res.data.data1;
    });
    let url1 = "server/personnel_management/get_sign_country/";
    this.$axios.get(this._url2 + url1).then(res => {
      this.countries = res.data.data;
    });
  },
  components: {
    FbmTemplate
  }
};
</script>

<style>
.deliverytemplate {
  position: relative;
  background-color: rgba(255, 255, 255, 0.9);
  width: 100%;
  padding: 30px 20px;
  display: flex;
  text-align: center;
  flex-direction: row;
  flex-wrap: nowrap;
}
.deliverytemplate h2 {
  margin: 30px 0 20px;
  font-size: 30px;
  text-align: left;
}
.deliverytemplate h3 {
  margin: 30px 0;
}
.deliverytemplate .leftmenu {
  display: flex;
  flex-direction: column;
  width: 300px;
}
.deliverytemplate .finallcheck {
  margin: 36px auto 0;
  background: #409eff;
  border: none;
  outline: none;
  padding: 10px 30px;
  color: #fff;
  border-radius: 4px;
}
.deliverytemplate .rightcon {
  flex: 1;
  margin-top: 24px;
}
.deliverytemplate ul {
  margin: 30px 25px;
}
.deliverytemplate ul li {
  border: 1px solid #e5e5e5;
  height: 35px;
  line-height: 35px;
  cursor: pointer;
}
.deliverytemplate .editlog {
  margin: 0 auto;
}
.deliverytemplate .submit {
  margin: 36px auto 0;
  background: #409eff;
  border: none;
  outline: none;
  padding: 10px 30px;
  color: #fff;
  border-radius: 4px;
}
li a {
  display: block;
  color: #000;
}
.active {
  background-color: #3a8ee6;
}
.active a {
  color: #fff;
}
.deliverytemplate .mask {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}
.deliverytemplate .showFinal {
  position: absolute;
  left: 50%;
  bottom: 350px;
  transform: translate(-50%, -50%);
  background: #fff;
  z-index: 1;
  border: 1px solid #e5e5e5;
  padding: 40px;
}
.deliverytemplate input[type="file"] {
  display: none !important;
}
</style>
<style>
.rightcon .el-form.el-form--inline {
  text-align: left;
}
</style>



