<template>
  <div class="fbm_template">
    <h2>{{ steplist.h2title }}</h2>
    <StepCard
      @upload="uploadflag"
      @enlargeImg="showNotice"
      v-for="(item, i) in steplist.others"
      :key="i"
      :hTitle="item.hTitle"
      :hTip="item.hTip"
      :imgSrc="item.imgSrc"
      :index="i"
      :submitflag="item.submitflag"
    ></StepCard>
    <!-- :url="item.uploadUrl" -->
    <div>
      <button class="finallcheck" @click="finallcheck">确认修改完成</button>
    </div>
    <div class="mask" v-show="showFinal">
      <div class="showFinal">
        <p>别忘了核对前台配送时效哦！</p>
        <button class="submit" @click="gotIt">确认完成</button>
      </div>
    </div>
    <div
      class="notice_dialog"
      :style="{ 'z-index': showDialog ? '1' : '-999' }"
      v-show="showDialog"
    >
      <img :src="'/static/images/' + bigSrc" alt="" />
      <span @click="showDialog = false" class="closeDialog">X</span>
    </div>
  </div>
</template>

<script>
// import StepCard from "@/components/orderManagement/FBM/StepCard";
import StepCard from "@/components/orderManagement/FBM/StepCardCopy";
export default {
  props: ["allStepData"],
  data() {
    return {
      uploadImg: "",
      showDialog: false,
      idx: "",
      count: [],
      bigSrc: "",
      showFinal: false,
      finalCommit: false
    };
  },
  computed: {
    steplist() {
      // 深拷贝
      let jsonStr = JSON.stringify(this.allStepData);
      let jsonObj = JSON.parse(jsonStr);
      return jsonObj;
    }
  },
  methods: {
    uploadflag(index) {
      if (this.count.indexOf(index) === -1) {
        this.count.push(index);
      }
    },
    showNotice(imgsrc) {
      this.showDialog = true;
      this.bigSrc = imgsrc;
      // 按照每一步的请求列成一个数组，根据数据的index发请求。
    },
    uploadFile() {
      // 1.获取input
      // 遍历获取多个file
      let files = this.$refs.tem_input.files;
      // todo  new formData???
      files = [].slice.call(files);
      files.forEach(i => {
        if (i.type && this.acceptTypes.indexOf(i.type) === -1) {
          alert("请上传图片类型！");
          return;
        }
      });
      this.steplist[this.idx].submitflag = true;
    },
    submitImg() {
      // 发送完请求  如果，$emit数据
      if (this.count.indexOf(this.idx) === -1) {
        this.count.push(this.idx);
      }
      this.$emit("finalcheck", this.count);
      // 上传图片
      this.showDialog = false;
      this.$refs.tem_input.value = "";
    },
    finallcheck() {
      if (this.count.length !== this.steplist.length) {
        alert("未上传全部图片,请检查！");
        return;
      }
      this.showFinal = true;
      // todo 是否需要发请求？列表中去除本待处理sku
      // this.due_skus.splice(this.curIdx, 1);
    },
    gotIt() {
      console.log(1);
      this.showFinal = false;
    }
  },
  components: {
    StepCard
  }
};
</script>

<style scoped>
.fbm_template {
  width: 800px;
}
.fbm_template .notice_dialog {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  left: 0;
  background: transparent;
}
.fbm_template .notice_dialog img {
  width: 100%;
  height: 100%;
}
.fbm_template .closeDialog {
  position: absolute;
  right: 50px;
}
.fbm_template .submitImg,
.fbm_template .closeDialog {
  margin: 36px auto 0;
  background: #409eff;
  border: none;
  outline: none;
  padding: 10px 30px;
  color: #fff;
  border-radius: 4px;
}
.fbm_template .notice_dialog p {
  margin: 70px 0 30px;
  font-size: 18px;
}
.notice_dialog input[type="file"] {
  width: 203px;
}
.fbm_template .mask {
  left: 0;
  position: absolute;
  top: 200px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}
</style>



