<template>
  <div class="stepCard">
    <h3>{{ hTitle }}</h3>
    <p v-if="hTip">{{ hTip }}</p>
    <div v-if="imgSrc.length">
      <img
        @click="enlarge(item)"
        v-for="(item, j) in imgSrc"
        :key="j"
        :src="'/static/images/' + item"
        alt=""
      />
    </div>
    <div class="confirm">
      <el-upload
        ref="upload"
        :on-remove="removefile"
        :file-list="fileList"
        drag
        accept="image/png, image/jpeg, image/gif, image/jpg"
        :action="url"
        multiple
        :before-upload="checkType"
        :http-request="uploadfile"
        :auto-upload="false"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传图片文件</div>
      </el-upload>
      <div>
        <button class="confirmBtn" @click="uploadSubmit">确认上传</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["hTitle", "hTip", "imgSrc", "index", "submitflag"],
  data() {
    return {
      file: "",
      fileList: [],
      canUpload: false, // 确认上传标志
      formData: null,
      acceptTypes: ["image/png", "image/jpeg", "image/gif", "image/jpg"]
    };
  },
  computed: {
    url() {
      return this._url2 + "server/order_management/upload_template/";
    }
  },
  methods: {
    submitFiles() {
      console.log(提交);
    },
    uploadfile(input) {
      // 如果有文件则可以上传
      this.canUpload = true;
      this.formData.append("file", input.file);
      console.log(this.index);
    },
    checkType(file) {
      console.log(file);
      if (file.type && this.acceptTypes.indexOf(file.type) === -1) {
        alert("请上传图片类型！");
        return;
      }
    },
    removefile(file, fileList) {
      if (!fileList.length) {
        this.canUpload = false;
      }
    },
    uploadSubmit() {
      // 点击确认上传，触发事件，执行到this.$refs.upload.submit 触发http-request事件，然后再 继续往下执行。
      this.formData = new FormData();
      this.$refs.upload.submit();
      if (!this.canUpload) {
        alert("请选择文件或确认上传！");
        return;
      }
      let data = this.$parent.$parent.formData;
      let sku = this.$parent.$parent.sku;
      let stepStr;
      switch (this.index) {
        case 0:
          stepStr = "step_one";
          break;
        case 1:
          stepStr = "step_two";
          break;
        case 2:
          stepStr = "step_three";
          break;
        case 3:
          stepStr = "step_four";
          break;
        case 4:
          stepStr = "step_five";
          break;
      }
      if (
        data.country &&
        data.store &&
        data.country !== "all" &&
        data.store !== "all" &&
        sku &&
        this.formData
      ) {
        this.formData.append("station", data.store);
        this.formData.append("country", data.country);
        this.formData.append("sku", sku);
        this.formData.append("step", stepStr);
        this.$axios.post(this.url, this.formData).then(res => {
          console.log(res);
          this.$emit("upload", index);
        });
      } else {
        alert("请检查是否选择国家，站点，sku及上传文件");
      }
    },
    enlarge(imgsrc) {
      console.log(imgsrc);
      this.$emit("enlargeImg", imgsrc);
    }
  }
};
</script>

<style scoped>
.stepCard {
  font-size: 16px;
  text-align: left;
  position: relative;
  min-height: 250px;
}
.stepCard h3 + p {
  margin: 10px 0;
}
.stepCard img {
  margin-bottom: 10px;
  width: 100%;
}
.confirm {
  display: flex;
  align-items: center;
  position: absolute;
  right: -550px;
  top: 50%;
  transform: translateY(-50%);
}
.confirmBtn {
  outline: none;
  border: none;
  padding: 15px 30px;
  border-radius: 5px;
  color: #fff;
  background-color: #409eff;
  margin-left: 20px;
}
</style>



